"use client";

import { useActionState, useEffect, useState } from "react";
import Dropdown from "@/components/Dropdown";
import ServiceSelect from "@/components/ServiceSelect";
import ImageUpload from "@/components/ImageUpload";
import Image from "next/image";
import { IMAGES } from "@/constants/images";
import { ICONS } from "@/constants/icons";
import {
  FileType,
  MediaType,
  MethodType,
  MimeType,
  OfferingType,
  SpecialistType,
  SpecialistResponseType,
} from "@/lib/types";
import { SpecialistCreateSchema } from "@/lib/validation";
import fetcher from "@/lib/fetcher";
import slug from "slug";
import { redirect } from "next/navigation";

interface HandleImageUploadEvent extends React.ChangeEvent<HTMLInputElement> {}

export default function CreateSection({
  specialistId,
}: {
  specialistId: string;
}) {
  const [fetchData, setFetchData] = useState<SpecialistResponseType | null>(
    null
  );
  const [isEditOpen, setIsEditOpen] = useState<boolean>(false);
  const [isPublishOpen, setIsPublishOpen] = useState<boolean>(false);
  const [selectedOption, setSelectedOption] = useState<OfferingType[]>([]);
  const [files, setFiles] = useState<FileType[]>([]);

  const handleImageUpload = (e: HandleImageUploadEvent, id: number) => {
    const file = e.target.files?.[0];
    if (!file) return;

    let data = {
      display_order: id,
      file_name: file.name,
      file_size: file.size,
      file_url: URL.createObjectURL(file),
      mime_type: file.type as MimeType,
      media_type: file.type.split("/")[1] as MediaType,
    };

    if (files.find((m) => m.display_order === data.display_order)) {
      const updatedFiles = files.map((m) =>
        m.display_order === data.display_order ? data : m
      );
      setFiles(updatedFiles);
    } else {
      setFiles([...files, data]);
    }
  };

  const handleImageRemove = (id: number) => {
    const updatedFiles = files.filter((m) => m.display_order !== id);
    setFiles(updatedFiles);
  };

  const handleAction = async (state: any, formData: FormData) => {
    const validatedData = SpecialistCreateSchema.safeParse({
      title: formData.get("title"),
      description: formData.get("description"),
      duration: formData.get("estimatedCompletionTime"),
      price: formData.get("price"),
      category: formData.get("serviceCategory"),
    });
    if (!validatedData.success) {
      return {
        errors: validatedData.error.flatten().fieldErrors,
        message: "Please check the form for errors",
        title: formData.get("title"),
        description: formData.get("description"),
        duration: formData.get("estimatedCompletionTime"),
        price: formData.get("price"),
        category: formData.get("serviceCategory"),
      };
    }
    if (selectedOption.length === 0) {
      return {
        errors: {
          offerings: ["Please select at least one additional offering"],
        },
        message: "Please check the form for errors",
        title: formData.get("title"),
        description: formData.get("description"),
        duration: formData.get("estimatedCompletionTime"),
        price: formData.get("price"),
        category: formData.get("serviceCategory"),
      };
    }
    if (files.length === 0) {
      return {
        errors: { files: ["Please upload at least one image"] },
        message: "Please check the form for errors",
        title: formData.get("title"),
        description: formData.get("description"),
        duration: formData.get("estimatedCompletionTime"),
        price: formData.get("price"),
        category: formData.get("serviceCategory"),
      };
    }
    const { title, description, duration, price, category } =
      validatedData.data;

    const feePercentage = 0.1; // 10% platform fee
    const platformFee = price * feePercentage;
    const finalPrice = price + platformFee;

    const data: SpecialistType = {
      title: title,
      slug: slug(title),
      description: description,
      base_price: parseInt(price.toFixed(2)),
      platform_fee: parseInt(platformFee.toFixed(2)),
      final_price: parseInt(finalPrice.toFixed(2)),
      fee_percentage: parseInt((feePercentage * 100).toFixed(2)),
      duration_days: duration,
      service_category: category,
      service_offerings: selectedOption,
      medias: files,
    };

    const result = await fetcher("specialists", MethodType.POST, data);
    if (!result.success) {
      return {
        success: false,
        message: result.message,
        title: formData.get("title"),
        description: formData.get("description"),
        duration: formData.get("estimatedCompletionTime"),
        price: formData.get("price"),
        category: formData.get("serviceCategory"),
      };
    }

    setIsEditOpen(false);
    setSelectedOption([]);
    setFiles([]);
    redirect(`/specialists/${result?.data}`);
  };

  const handlePublish = async () => {
    const result = await fetcher(
      `specialists/${fetchData?.id}/publish`,
      MethodType.PUT,
      { is_draft: false }
    );
    if (result.success) {
      throw new Error("Failed to publish specialist");
    }

    setIsPublishOpen(!isPublishOpen);
  };

  useEffect(() => {
    const fetchSingleSpecialist = async () => {
      if (specialistId !== "create") {
        const result = await fetcher(
          `specialists/${specialistId}`,
          MethodType.GET
        );
        if (!result.success) {
          throw new Error("Failed to fetch specialist data");
        }

        const data = result.data;
        if (data) {
          setFetchData(data);
          setSelectedOption(data.service_offerings);
          console.log("data.service_offerings :>> ", data.service_offerings);
          setFiles(data.medias);
        }
      }
    };

    fetchSingleSpecialist();
  }, [specialistId]);

  const [state, action, isPending] = useActionState(handleAction, undefined);
  return (
    <main className="flex-1 overflow-y-scroll">
      <section className="flex flex-wrap px-3 pt-7.75 gap-5">
        <div className="w-106 space-y-3">
          <div>
            <h2 className="font-semibold text-lg text-[#222222]">
              {fetchData?.title || ""}
            </h2>
            <div className="flex flex-wrap gap-0.5">
              <div className="flex items-center justify-center flex-col w-57.25 bg-[#F5F5F5]">
                <Image src={ICONS.photoScan} alt="photo scan icon" />
                <p className="w-34.5 font-semibold text-[5px] text-[#888888]">
                  Upload an image for your service listing in PNG, JPG or JPEG
                  up to 4MB
                </p>
              </div>
              <div className="space-y-px w-48">
                <Image src={IMAGES.banner1} alt="banner 1" />
                <Image src={IMAGES.banner2} alt="banner 2" />
              </div>
            </div>
          </div>
          <div className="border-b-[0.5px] border-[#E0E0E0] pb-4">
            <h3 className="font-bold text-[11px] text-[#222222] mb-2">
              Description
            </h3>
            <p className="font-semibold text-[7px] text-[#888888]">
              {fetchData?.description || "Describe your service here"}
            </p>
          </div>
          <div className="border-b-[0.5px] border-[#E0E0E0] pb-4">
            <h3 className="font-bold text-[11px] text-[#222222] mb-2">
              Additional Offerings
            </h3>
            <p className="font-semibold text-[7px] text-[#888888]">
              {fetchData?.service_offerings
                ?.map((so) => so.service_name)
                .join(", ") || "No additional offerings selected"}
            </p>
          </div>
          <div className="border-b-[0.5px] border-[#E0E0E0] pb-4">
            <h3 className="font-bold text-[11px] text-[#222222] mb-2">
              Company Secretary
            </h3>
            <div className="grid grid-cols-2 gap-3">
              <div>
                <div className="flex items-center gap-1 mb-2">
                  <Image
                    src={IMAGES.user3}
                    alt="user"
                    className="w-7.5 h-7.5 rounded-full"
                  />
                  <div>
                    <h5 className="flex items-center">
                      <span className="font-semibold text-[6px] text-[#222222]">
                        Grace Lam
                      </span>
                      <Image
                        src={ICONS.verified}
                        alt="verified icon"
                        className="ml-1 mr-px"
                      />
                      <span className="font-semibold text-[3px] text-[#454545]">
                        Verified
                      </span>
                    </h5>
                    <h6 className="font-semibold text-[4.5px] text-[#454545] -mt-0.5 mb-0.5">
                      Corpsec Services Sdn Bhd
                    </h6>
                    <a
                      href=""
                      className="flex items-center justify-center bg-[#071331] h-2.5 w-8.75 rounded-[1.5px] font-semibold text-[4px] text-white"
                    >
                      View Profile
                    </a>
                  </div>
                </div>
                <p className="font-semibold text-[4.5px] leading-1.5 text-[#454545]">
                  A company secretarial service founded by Grace, who believes
                  that every company deserves clarity, confidence, and care in
                  their compliance journey. Inspired by the spirit of
                  entrepreneurship, Aida treats every clients business as if it
                  were her own  attentive to detail, committed to deadlines,
                  and focused on growth. Step into a partnership built on trust,
                  transparency, and professional excellence. Whether youre just
                  starting out or managing a growing company, Aida is here to
                  make your corporate governance smooth, secure, and
                  stress-free. Your companys peace of mind starts here
                </p>
              </div>
              <div>
                <h6 className="font-semibold text-[8px] text-[#222222]">
                  Certified Company Secretary
                </h6>
                <ul className="flex items-center gap-1">
                  <li>
                    <Image
                      className="w-4.5"
                      src={IMAGES.company1}
                      alt="company 1"
                    />
                  </li>
                  <li>
                    <Image
                      className="w-4.5"
                      src={IMAGES.company2}
                      alt="company 2"
                    />
                  </li>
                  <li>
                    <Image
                      className="w-4.5"
                      src={IMAGES.company3}
                      alt="company 3"
                    />
                  </li>
                </ul>
              </div>
            </div>
          </div>
        </div>
        <div className="flex-1 space-y-2">
          <div className="flex gap-0.5">
            <button
              onClick={() => setIsEditOpen(!isEditOpen)}
              className="flex items-center justify-center gap-1 h-5.25 w-14.75 rounded-[3px] bg-[#071331] text-white text-[7px]"
            >
              Edit
            </button>
            <button
              onClick={() => setIsPublishOpen(!isPublishOpen)}
              className="flex items-center justify-center gap-1 h-5.25 w-14.75 rounded-[3px] bg-[#002F70] text-white text-[7px]"
            >
              Publish
            </button>
          </div>
          <div className="bg-white px-3 py-3 x-shadow-3">
            <h2 className="font-semibold text-lg">Professional Fee</h2>
            <p className="font-semibold text-[9px] text-[#888888] -mt-1">
              Set a rate for your service
            </p>
            <div className="flex items-center justify-center my-3">
              <p className="font-red-hot font-semibold leading-6 text-xl border-b-[1.5px] border-[#222222]">
                RM {fetchData?.final_price || "0.00"}
              </p>
            </div>
            <div className="">
              <p className="flex flex-wrap">
                <span className="flex-1 font-semibold text-[9px] text-[#454545]">
                  Base price
                </span>
                <span className="w-12 font-semibold text-[9px] text-[#222222]">
                  RM {fetchData?.base_price || "0.00"}
                </span>
              </p>
              <p className="flex flex-wrap">
                <span className="flex-1 font-semibold text-[9px] text-[#454545] underline">
                  Service processing fee
                </span>
                <span className="w-12 font-semibold text-[9px] text-[#222222]">
                  RM {fetchData?.platform_fee || "0.00"}
                </span>
              </p>
              <p className="flex flex-wrap border-b-[0.5px] border-[#888888] mb-2">
                <span className="flex-1 font-semibold text-[9px] text-[#454545]">
                  Total
                </span>
                <span className="w-12 font-semibold text-[9px] text-[#222222]">
                  RM {fetchData?.final_price}
                </span>
              </p>
              <p className="flex flex-wrap">
                <span className="flex-1 font-semibold text-[9px] text-[#454545]">
                  Your returns
                </span>
                <span className="w-12 font-semibold text-[9px] text-[#222222]">
                  RM {fetchData?.final_price || "0.00"}
                </span>
              </p>
            </div>
          </div>
        </div>
      </section>
      <div
        className={`${
          isEditOpen ? "absolute" : "hidden"
        } inset-0 min-h-screen h-full bg-black/15 z-30`}
      >
        <div className="relative w-68.25  min-h-screen max-h-full overflow-y-scroll bg-white ml-auto px-4 py-3 z-20">
          <div className="flex items-center justify-between">
            <h3 className="font-red-hot font-semibold text-xs">Edit Service</h3>
            <button onClick={() => setIsEditOpen(!isEditOpen)}>
              <Image src={ICONS.close} alt="Close" />
            </button>
          </div>
          <form action={action} className="mt-4">
            <div className="flex flex-col mb-3">
              <label
                htmlFor="title"
                className="font-semibold text-[7px] text-[#454545]"
              >
                Title
              </label>
              <input
                type="text"
                id="title"
                name="title"
                placeholder="Enter your service title"
                defaultValue={
                  typeof state?.title === "string"
                    ? state.title
                    : fetchData?.title || ""
                }
                className="border-[0.2px] border-[#888888] rounded-xs h-4.75 font-semibold text-[5px] px-1.5 text-[#181818]"
              />
              {state?.errors &&
                "title" in state.errors &&
                state.errors.title && (
                  <p className="text-red-500 text-[5px]">
                    {state.errors.title}
                  </p>
                )}
            </div>
            <div className="flex flex-col">
              <label
                htmlFor="description"
                className="font-semibold text-[7px] text-[#454545]"
              >
                Description
              </label>
              <textarea
                id="description"
                name="description"
                defaultValue={
                  typeof state?.description === "string"
                    ? state.description
                    : fetchData?.description || ""
                }
                placeholder="Describe your service here"
                className="border-[0.2px] border-[#888888] rounded-xs h-16 font-semibold text-[5px] pt-1 px-1.5 text-[#181818]"
              ></textarea>
              {state?.errors &&
                "description" in state.errors &&
                state.errors.description && (
                  <p className="text-red-500 text-[5px]">
                    {state.errors.description}
                  </p>
                )}
              <span className="font-semibold text-[5px] ml-auto text-[#222222]">
                (500 words)
              </span>
            </div>
            <Dropdown
              id="estimatedCompletionTime"
              labels={[
                "1 day",
                "2 days",
                "3 days",
                "4 days",
                "5 days",
                "6 days",
              ]}
              values={[1, 2, 3, 4, 5, 6]}
              label="Estimated Completion Time (Days)"
              defaultValue={
                typeof state?.duration === "string"
                  ? state.duration
                  : fetchData?.duration_days || ""
              }
            >
              {state?.errors &&
                "duration" in state.errors &&
                state.errors.duration && (
                  <p className="text-red-500 text-[5px]">
                    {state.errors.duration}
                  </p>
                )}
            </Dropdown>
            <div className="flex flex-col mb-3">
              <label
                htmlFor="price"
                className="font-semibold text-[7px] text-[#454545]"
              >
                Price
              </label>
              <div className="flex h-4.75">
                <div className="flex items-center gap-0.5 px-1.5 bg-[#F5F5F5] border-[0.2px] border-r-0  rounded-l-xs">
                  <Image src={IMAGES.flag} alt="flag" className="w-2.75" />
                  <span className="text-[7px] text-[#222222]">MYR</span>
                </div>
                <input
                  type="text"
                  id="price"
                  name="price"
                  defaultValue={
                    typeof state?.price === "string"
                      ? state.price
                      : fetchData?.base_price || ""
                  }
                  placeholder="0.00"
                  className="flex-1 text-[7px] px-1.5 border-[0.2px] border-black border-l-0 rounded-r-xs font-semibold text-[#181818]"
                />
              </div>
              {state?.errors &&
                "price" in state.errors &&
                state.errors.price && (
                  <p className="text-red-500 text-[5px]">
                    {state.errors.price}
                  </p>
                )}
            </div>
            <Dropdown
              labels={[
                "Incorporation of a new company",
                "Monthly Company Secretary subscription",
                "Opening of Bank Account",
                "Appointment of Secretary",
                "Appointment/Resignation of Director",
                "Change of Nature of Business",
              ]}
              values={[
                "Incorporation of a new company",
                "Monthly Company Secretary subscription",
                "Opening of Bank Account",
                "Appointment of Secretary",
                "Appointment/Resignation of Director",
                "Change of Nature of Business",
              ]}
              defaultValue={
                typeof state?.category === "string"
                  ? state.category
                  : fetchData?.service_category ||
                    "Incorporation of a new company"
              }
              label="Service category"
              id="serviceCategory"
            >
              {state?.errors &&
                "category" in state.errors &&
                state.errors.category && (
                  <p className="text-red-500 text-[5px]">
                    {state.errors.category}
                  </p>
                )}
            </Dropdown>
            <ServiceSelect
              selectedOption={selectedOption}
              setSelectedOption={setSelectedOption}
            >
              {state?.errors &&
                "offerings" in state.errors &&
                state.errors.offerings && (
                  <p className="text-red-500 text-[5px]">
                    {state.errors.offerings}
                  </p>
                )}
            </ServiceSelect>
            <ImageUpload
              data={files.find((m) => m.display_order === 1)}
              handleUpload={handleImageUpload}
              handleRemove={handleImageRemove}
              id="1"
              position="1st"
            >
              {state?.errors &&
                "files" in state.errors &&
                state.errors.files && (
                  <p className="text-red-500 text-[5px]">
                    {state.errors.files}
                  </p>
                )}
            </ImageUpload>
            {files.length >= 1 && (
              <ImageUpload
                data={files.find((m) => m.display_order === 2)}
                handleUpload={handleImageUpload}
                handleRemove={handleImageRemove}
                id="2"
                position="2nd"
              />
            )}
            {files.length >= 2 && (
              <ImageUpload
                data={files.find((m) => m.display_order === 3)}
                handleUpload={handleImageUpload}
                handleRemove={handleImageRemove}
                id="3"
                position="3rd"
              />
            )}
            <div className="flex gap-1 mt-6">
              <button
                type="reset"
                onClick={() => setIsEditOpen(false)}
                className="w-14.75 h-5 border-[0.7px] border-[#C4C4C4] rounded-[2.5px] text-[6px] text-[#C00306]"
              >
                Cancel
              </button>
              <button
                type="submit"
                disabled={isPending}
                className="w-14.75 h-5 rounded-[2.5px] bg-[#002F70] text-[6px] text-white"
              >
                Confirm
              </button>
            </div>
          </form>
        </div>
        <div
          onClick={() => setIsEditOpen(false)}
          className="absolute inset-0 z-10"
        ></div>
      </div>
      <div
        className={`${
          isPublishOpen ? "absolute" : "hidden"
        } flex items-center justify-center inset-0 h-screen bg-black/80 z-50`}
      >
        <div className="relative w-107 rounded-[5px] px-3 py-2 bg-white z-20">
          <div>
            <h4 className="flex items-center gap-1">
              <Image src={ICONS.info} alt="" />
              <span className="font-red-hot font-bold text-xl text-[#222222]">
                Publish changes
              </span>
            </h4>
            <p className="text-[10px] text-[#454545]">
              Do you want to publish these changes? It will appear in the
              marketplace listing
            </p>
          </div>
          <div className="flex items-center justify-end gap-2 mt-4">
            <button
              onClick={() => setIsPublishOpen(false)}
              type="button"
              className="w-22.25 h-6 border-[0.6px] border-[#222222] rounded-[3px] font-semibold text-[8px] text-[#222222]"
            >
              Continue Editing
            </button>
            <button
              onClick={handlePublish}
              className="w-22.25 h-6 rounded-[3px] bg-[#002F70] text-[8px] text-white"
            >
              Save changes
            </button>
          </div>
        </div>
        <div
          onClick={() => setIsPublishOpen(false)}
          className="absolute inset-0 z-10"
        ></div>
      </div>
    </main>
  );
}
